<script>
  import {
    content
  } from '../stores.js';
  export let data;
  
  const ellipsis = (str, max = 30) => {
    return (str.length > max  ? str.substr(0, max) + '...' : str);
  }

  let showPost = () => {
    $content.actions.goPost(data);
  }
</script>

<style>
  .media-content {
    min-height: 55px;
  }
  .tags {
    max-height: 30px;
    overflow-y: hidden;
  }
  .tag {
    margin-right: 3px;
  }
</style>

<div class="card">
  <div class="card-image">
    <figure class="image is-4by3">
      <img src="{data.cover}" alt="Placeholder image">
    </figure>
  </div>
  <div class="card-content">
    <div class="media">
      <div class="media-left">
        <figure class="image is-48x48">
          <img src="{data.by.photo}" alt="Placeholder image">
        </figure>
      </div>
      <div class="media-content">
        <p class="title is-4 is-size-6">{ellipsis(data.title, 20)}</p>
        <p class="subtitle is-6 is-size-7">{data.category}</p>
      </div>
    </div>

    <div class="content">
      <div class="is-size-6">{ellipsis(data.body, 100)}</div>

      <div class="tags">
        {#each data.tags.split(' ').splice(0, 3) as tag}
          <span class="tag is-primary is-size-7">#{tag}</span>
        {/each}
      </div>

      <span class="is-size-7 has-text-grey">{data.by.name} ~ {data.post_at}</span>
      <div>
        <a class="is-size-7" href="javascript:void(0)" on:click={showPost}>Read More...</a>
      </div>
    </div>
  </div>
</div>